import React from "react";
const Header = ({ clickHandler, InputHandler }) => {
  return (
    <header className="row text-center" id="header">
      <h1 className="col-md-12">IP address tracker</h1>
      <div className="col-md-12">
        <input
          type="text"
          placeholder="Search any ip address or domain"
          className="w-50 mx-auto h-75 mb-3"
          onChange={InputHandler}
        />
        <button className="btn " onClick={clickHandler}>
          Search
        </button>
      </div>
    </header>
  );
};

export default Header;
