import React from "react";

const Items = ({ item }) => {
  return (
    <div className="container">
      {item.map((info, index) => {
        return (
          <div className="row" key={index}>
            <div className="col-md-3">
              <h2>Ip address</h2>
              <p>{info.ip}</p>
            </div>
            <div className="col-md-3">
              <h2>location</h2>
              <p>{info.location.city}</p>
            </div>
            <div className="col-md-3">
              <h2>timezone</h2>
              <p>{info.location.timezone}</p>
            </div>
            <div className="col-md-3">
              <h2>Isp</h2>
              <p>{info.isp}</p>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default Items;
