import axios from "axios";
import React, { useEffect, useState } from "react";
import {
  MapContainer,
  TileLayer,
  Marker,
  Circle,
  useMap,
  Pane,
} from "react-leaflet";
import Items from "./Items";
const Map = ({ click }) => {
  const [Ip, setIp] = useState();
  const [currentPos, setcurrentPos] = useState({
    lat: "",
    lng: "",
  });

  function ChangeMapView({ coords }) {
    const map = useMap();
    map.setView(coords, map.getZoom());

    return null;
  }

  useEffect(() => {
    let API = `https://geo.ipify.org/api/v1?apiKey=at_BZdubqTDSUYV4zH2wM04W5mmB76nw&ipAddress`;

    if (click) {
      API = `https://geo.ipify.org/api/v1?apiKey=at_BZdubqTDSUYV4zH2wM04W5mmB76nw&ipAddress=${click}`;
    }

    axios.get(API).then((res) => {
      setIp([res.data]);
      setcurrentPos({
        lat: res.data.location.lat,
        lng: res.data.location.lng,
      });
    });
  }, [click]);
  console.log(currentPos);
  return (
    <div className="row">
      <MapContainer
        center={currentPos}
        zoom={13}
        scrollWheelZoom={false}
        className="map"
      >
        <TileLayer
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />

        <Marker position={currentPos} />
        <Pane name="custom" style={{ zIndex: 100 }}>
          <Circle center={currentPos} radius={200} />
        </Pane>
        <ChangeMapView coords={currentPos} />
      </MapContainer>
      {Ip && <Items item={Ip} />}
    </div>
  );
};

export default Map;
